.user-view {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .user-text {
        .user-text-up {
            display: block;
        }

        .user-text-down {
            display: block;
            font-size: 0.75rem;
        }
    }
}

.user-view-md {
    .user-text {
        font-size: 0.75rem;
    }

    .user-text-up {
        font-weight: bold;
    }
}

.user-view-sm {
    display: inline-flex;

    .user-img {
        width: 25px;
        text-align: center;
    }
}
