.client-view {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .client-text {
        .client-text-up {
            display: block;
        }

        .client-text-down {
            display: block;
            font-size: 0.75rem;
        }
    }
}

.client-view-md {
    .client-text {
        font-size: 0.75rem;
    }

    .client-text-up {
        font-weight: bold;
    }
}

.client-view-sm {
    display: inline-flex;

    .client-img {
        width: 25px;
        text-align: center;
    }
}
