.target-view {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .target-text {
        .target-name {
            display: block;
        }

        .target-handle {
            display: block;
        }
    }
}

.target-view-lg {
    img {
        width: 50px;
        margin-right: 10px;
    }

    .target-info {
        font-size: 0.75rem;
    }
}

.target-view-md {
    img {
        width: 35px;
        margin-right: 5px;
    }

    .target-text {
        font-size: 0.75rem;
    }

    .target-name {
        font-weight: bold;
    }
}

.target-view-sm {
    display: inline-flex;

    .target-img {
        width: 25px;
        text-align: center;
    }

    img {
        position: relative;
        top: -1px;
        width: 15px;
    }
}
