.report-view {
    display: flexbox;
    justify-content: flex-start;
    align-items: center;

    time {
        display: block;
        font-size: 0.75em;
    }

    .report-download {
        text-transform: uppercase;
        font-size: 0.85em;
    }
}

.report-view-lg {
    .report-name {
        font-weight: bold;
    }
}

.report-view-md {
    font-size: 0.75rem;

    .report-name {
        font-weight: bold;
    }
}
